.fadein {
  animation: (550ms) fadein;
  animation-fill-mode: forwards;
  opacity: 0;
  transform: translate(0, -2rem*2); }

.slideright {
  animation: (550ms) slideright;
  animation-fill-mode: forwards;
  opacity: 0;
  transform: translate(--2rem, 0); }

.slideleft {
  animation: (550ms) slideleft;
  animation-fill-mode: forwards;
  opacity: 0;
  transform: translate(-2rem, 0); }

@keyframes fadein {
  0% {
    opacity: 0;
    transform: translate(0, -2rem); }
  100% {
    opacity: 1;
    transform: translate(0, 0); } }

@keyframes slideright {
  0% {
    opacity: 0;
    transform: translate((--2rem), 0); }
  100% {
    opacity: 1;
    transform: translate(0, 0); } }

@keyframes slideleft {
  0% {
    opacity: 0;
    transform: translate(-2rem, 0); }
  100% {
    opacity: 1;
    transform: translate(0, 0); } }

